}

.icons-section .github:hover {
    fill: #24292F;
}

.timeline-container {
    display: flex;
    justify-content: center;
    padding-bottom: 5vh;
}

.vertical-scrollable-timeline {
    width: 70vw;
    list-style-type: none;
    position: relative;
}

.list-progress {
    width: 4px;
    height: 100%;
    background-color: #444444;
    position: absolute;
    left: 10px;
    top: 0;
    overflow: hidden;
}

.list-progress .inner {
    position: absolute;
    right: 0;
    bottom: 0;
    height: 100%;
    background-color: #999999;
    width: 100%;
}

.vertical-scrollable-timeline li {
    position: relative;
    padding: 0 0 36px 48px;
}

.vertical-scrollable-timeline li:last-child {
    margin-bottom: 0;
}

.vertical-scrollable-timeline li p:last-child {
    margin-bottom: 0;
}

.vertical-scrollable-timeline li .pointer {
    position: absolute;
    left: 0;
    top: 0;
    width: 24px;
    height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #999999;
    border-radius: 50%;
    z-index: 1;
    transition: 0.4s all;
}

.vertical-scrollable-timeline li .pointer::before {
    content: '';
    width: 20px;
    height: 20px;
    border: 2px solid #fff;
    position: absolute;
    background-color: #999999;
    border-radius: 50%;
    z-index: -1;
    transition: 0.4s all;
}

.vertical-scrollable-timeline li::before {
    position: absolute;
    height: 100%;
    width: 8px;
    background-color: transparent;
    left: 48px;
    z-index: 0;
}

.vertical-scrollable-timeline li.active .pointer {
    background-color: #cb0909;
}

.vertical-scrollable-timeline li.active .pointer::before {
    background-color: #ce4646;
}

